.header {
    background-color: $white;
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 25px 20px 25px 20px;
    }
    &__logo {
        max-width: 90px;
        @media(max-width: $tablet) {
            display: none;
        }
        &-text {
            font-family: 'SebastianBold';
            font-size: 25px;
            color: #474e5b;
            @media(max-width: $tablet) {
                display: none;
            }
        }
        &-line {
            margin: 0px 20px 0px 20px;
            fill: #474e5b;
            @media(max-width: $tabletWide) {
                display: none;
            }
        }
    }
    &__start {
        &-tel {
            font-size: 21px;
            font-weight: bold;
            transition: $transition;
            &:hover,
            &:focus,
            &:active {
                color: $text_link;
            }
        }
        &-subtitle {
            font-size: 13px;
            color: $text_default;
        }
    }
    &__middle {
        display: flex;
        align-items: center;
        &-info {
            font-size: 13px;
            color: #474e5b;
            @media(max-width: $tabletWide) {
                display: none;
            }
        }
    }
    &__end {
        display: flex;
        align-items: center;
        @media(max-width: $tablet) {
            display: none;
        }
        &-id {
            margin-right: 20px;
        }
    }
}
